    //
    @action.bound async getData(type, params) {
        this.isLoading = true
        //123
        const url = type === 1 ? services.requestUrl.departmentList : type === 2 ? services.requestUrl.roleList : services.requestUrl.userList
        try {
            let res = await services.getRequest(url, params)
            this.isLoading = false
            if (isDataExist(res)) {
                if (type === 1) {
                    this.tableData = res.data.data
                } else {
                    this.tableData = res.data.data.items
                    this.PageInfo = res.data.data.page
                }
            }
        } catch (error) {
            console.log(error)
        }
    }
    //
    @action.bound async getDeptData(type, params) {
        this.isLoading = true
        //12
        const url = type === '1' ? services.requestUrl.departmentList : services.requestUrl.getCustomer
        try {
            let res = await services.getRequest(url, params)
            this.isLoading = false
            if (isDataExist(res)) {
                if (type === '1') {
                    this.arr = res.data.data
                    // console.log(this.arr)
                    const data = this.transTreeData(this.arr)
                    this.tableData = data
                    // this.tableData = res.data.data.children

                } else {
                    this.tableData = res.data.data
                }
            }
        } catch (error) {
            console.log(error)
        }
    }
