import { observable, action, } from 'mobx';
import { isDataExist } from 'Utils/dataTools';
import * as services from '../services/work';
import { message } from 'antd';

class Work {
  @observable workList = []
  @observable workPage = {}
  @observable isLoading = false
  @observable status = 'add'
  @observable currentMain = {}
  @observable visible = false
  @observable searchCondition = {}
  @observable current = '1'
  @observable isDaily = false
  @observable workLog = []
  @observable workDetailList = []
  @observable workerAuth = {}
  @observable detailLogId = ''
  @observable detailVisible = false
  @observable logDetailList = []
  @observable workerName = ''
  @observable workCols = []

  @action async getWork(params) {
    this.isLoading = true
    try {
      let data = {
        pageIndex: 1,
        pageSize: 10,
        ...this.searchCondition,
        ...params
      }
      if (this.current === '1') {
        data.init = true;
      } else if (this.current === '2') {
        data.progress = true;
      } else {
        data.finish = true;
      }
      let res = await services.gets('getWork')(data)
      this.isLoading = false
      if (isDataExist(res)) {
        this.workList = res.data.data.items;
        this.workPage = res.data.data.page;
        this.workCols = res.data.dataExt;
      }
    } catch (error) {
      console.log(error)
    }
  }
  @action async saveWork(params) {
    this.isLoading = true
    try {
      let res = await services.posts('saveWork')(params)
      this.isLoading = false
      if (isDataExist(res)) {
        message.success('')
      }
    } catch (error) {
      console.log(error)
    }
    this.getWork()
  }
  @action async deleteWork(params) {
    try {
      let res = await services.posts('deleteWork')(params)
      if (isDataExist(res)) {
        message.success('')
      }
    } catch (error) {
      console.log(error)
    }
    this.getWork()
  }

  @action async getWorkLog(params) {
    try {
      let res = await services.gets('getWorkLog')(params)
      if (isDataExist(res)) {
        this.workLog = res.data.data;
      }
    } catch (error) {
      console.log(error);
    }
  }

  /*  */
  @action async getWorkAuth() {
    try {
      let res = await services.gets('getWorkAuth')();
      if (isDataExist(res)) {
        this.workerAuth = res.data.data;
      }
    } catch (error) {
      console.log(error);

    }
  }
  /*  */
  @action async getDetailLog(params) {
    try {
      let res = await services.gets('getDetailLog')(params)
      if (isDataExist(res)) {
        this.logDetailList = res.data.data;
      }
    } catch (error) {
      console.log(error);
    }
  }
  /*  */
  @action async getCountByWorkerId(params) {
    try {
      let res = await services.gets('getCountByWorkerId')(params)
      if (isDataExist(res)) {
        // res.data.data.workerName = this.workerName
        this.workDetailList = res.data.data;
      }
    } catch (error) {
      console.log(error);
    }
  }


  @action changeValue(key, value) {
    this[key] = value;
  }
}
let WorkStore = new Work();
export default WorkStore;