import './index.less';
import React from 'react';

export default ({ isLoading, error }) => {
  if (isLoading) {
    return <div className="component-loading">
      <p></p>
    </div>;
  } else if (error) {
    return <div className="component-loading">
      <p className="wrong-msg">
        
        </p>
    </div>;
  } else {
    return null;
  }
};