import { observable, action, } from 'mobx';
import { isDataExist } from 'Utils/dataTools';
import * as services from '../services/statistics';

class Statistics {
    @observable isLoading = false;
    @observable columnsType = 1;
    @observable tableData = [];//
    @observable searchDate = [];
    @observable pageInfo = { pageIndex: 1, pageSize: 10 };//
    @observable modalVisible = false;
    @observable detailInfo = {};
    @observable breakenInfo = [];
    @observable breakenPage = {};
    @observable logInfo = {};
    @observable workInfo = [];
    @observable deviceId = '';

    @action.bound async getData(type, params) {
        this.isLoading = true
        //1234
        const url = type === 1 ? services.requestUrl.deviceList : type === 2 ? services.requestUrl.spareList : type === 3 ? services.requestUrl.breakenList : services.requestUrl.staffList
        try {
            let res = await services.getRequest(url, params)
            this.isLoading = false
            if (isDataExist(res)) {
                this.tableData = res.data.data.items
                this.pageInfo = res.data.data.page
            }
        } catch (error) {
            console.log(error)
        }
    }
    @action.bound async getBreakenInfo(params) {
        this.isLoading = true
        try {
            let res = await services.getRequest(services.requestUrl.getBreakenInfo, params)
            this.isLoading = false
            if (isDataExist(res)) {
                this.breakenInfo = this.breakenInfo.concat(res.data.data.items)
                this.breakenPage = res.data.data.page
            }
        } catch (error) {
            console.log(error)
        }
    }
    @action.bound async getAllWorkInfo(params) {
        this.isLoading = true
        try {
            let res = await services.getRequest(services.requestUrl.getAllWorkInfo, { isSelf: true, ...params })
            this.isLoading = false
            if (isDataExist(res)) {
                this.workInfo = res.data.data
            }
        } catch (error) {
            console.log(error)
        }
    }
    @action.bound async getByLogId(params) {
        this.isLoading = true
        try {
            let res = await services.getRequest(services.requestUrl.getByLogId, params)
            this.isLoading = false
            if (isDataExist(res)) {
                this.logInfo = res.data.data
            }
        } catch (error) {
            console.log(error)
        }
    }
    @action.bound async exportDate(type, params) {
        //1234
        let url = type === 1 ? services.requestUrl.exportDevice : type === 2 ? services.requestUrl.exportSpare : type === 3 ? services.requestUrl.exportBreaken : services.requestUrl.exportStaff
        url = `${url}?pageIndex=${this.pageInfo.pageIndex}&pageSize=${this.pageInfo.pageSize}&startDate=${this.searchDate[0] ? this.searchDate[0] : ''}&endDate=${this.searchDate[1] ? this.searchDate[1] : ''} `
        // console.log(url)
        window.open(url)
        // try {
        //     let res = await services.getRequest(url, params)
        //     this.isLoading = false
        //     if (isDataExist(res)) {
        //         return res
        //     }
        // } catch (error) {
        //     console.log(error)
        // }
    }
    @action.bound async clearData() {
        this.columnsType = 1;
        this.tableData = [];
    }
    @action modalChange = () => {
        // console.log(this.modalVisible)
        this.modalVisible = !this.modalVisible
    }
}
let StatisticsStore = new Statistics();
export default StatisticsStore;
