import { observable, action } from 'mobx';
import { isDataExist } from 'Utils/dataTools';
import * as services from '../services/basic';
import { message } from 'antd';
// import { moment } from 'moment';

class DeviceMaintain {
	/*  */
	@observable isLoading = false;
	@observable deviceList = [];
	@observable serverList = [];
	@observable schemaList = [];
	@observable schemaPage = {};
	@observable deviceType = '';
	@observable maintainType = 1;
	@observable planType = 1;

	/*  */
	@observable currentMain = {};
	@observable editVisible = false

	@action async getDevice() {
		/*  */
		this.isLoading = true;
		try {
			let res = await services.gets('getSelectType')();
			this.isLoading = false;
			if (isDataExist(res)) {
				this.deviceList = res.data.data;
				this.deviceType = res.data.data[0].id;
			}
		} catch (error) {
			console.log(error);
		}
	}

	/* ----------------  */

	@action async getMaintainServer(params = {}) {
		/*  */
		this.isLoading = true;
		try {
			let res = await services.gets('getMaintainServer')({
				maintainType: this.maintainType,
				...params
			});
			this.isLoading = false;
			if (isDataExist(res)) {
				this.serverList = res.data.data;
			}
		} catch (error) {
			console.log(error);
		}
	}
	@action async getMaintainSchema(params = {}) {
		/*  */
		this.isLoading = true;
		try {
			let res = await services.gets('getMaintainSchema')({
				// typeId: this.deviceType,
				pageIndex: 1,
				pageSize: 10,
				maintainType: this.maintainType,
				...params
			});
			this.isLoading = false;
			if (isDataExist(res)) {
				this.schemaList = res.data.data.items;
				this.schemaPage = res.data.data.pageInfo;
			}
		} catch (error) {
			console.log(error);
		}
	}

	@action async save(params, type) {
		try {
			let req = type === 'server' ? 'saveServer' : 'saveSchema';
			let data = {
				...params,
				typeId: this.deviceType
			}
			// console.log(this.status,data);
			let res = await services.posts(req)(data);
			if (isDataExist(res)) {
				message.success('');
			}
		} catch (error) {
			console.log(error)
		}
		type === 'server' ? this.getMaintainServer() : this.getMaintainSchema({ typeId: this.deviceType });
	}
	@action async delete(params, type) {
		try {
			let req = type === 'server' ? 'deleteServer' : 'deleteSchema';
			let res = await services.posts(req)(params);
			if (isDataExist(res)) {
				message.success('');
			}
		} catch (error) {
			console.log(error)
		}
		type === 'server' ? this.getMaintainServer() : this.getMaintainSchema();
	}
	@observable currentMainServer = {}
	@observable currentMainSchame = {}
	@action async getInfo(params, type) {
		try {
			let req = type === 'server' ? 'getServerInfo' : 'getSchemaInfo';
			let res = await services.gets(req)(params);
			if (isDataExist(res)) {
				if (type === 'server') {
					this.currentMainServer = res.data.data;
				} else {
					this.currentMainSchame = res.data.data;

				}
			}
		} catch (error) {
			console.log(error)
		}
	}



}

let DeviceMaintainStore = new DeviceMaintain();
export default DeviceMaintainStore;
