import React,{PureComponent} from 'react';
import { 
  BrowserRouter as Router,
  // Redirect,
  Route,
  Switch
} from 'react-router-dom';

/*  */
import { 
  HomeRouter,
  LoginPage,StudioPage,StudioDevice,
  ZWordView
} from './home';

export default class RootRouter extends PureComponent{
  render(){
    return (
      <Router >
        <Switch>
          <Route path='/login' component={LoginPage} />
          <Route path='/iotstudio' component={StudioPage} />
          <Route path='/iot/device' component={StudioDevice} />
          <Route path='/iot/doc' component={ZWordView} />
          <Route path='/' component={HomeRouter} />
        </Switch>
      </Router>
    );
  }
}



