import { observable, action } from 'mobx';
import { isDataExist } from 'Utils/dataTools';
import * as services from '../services/zcloud';
import { message } from 'antd';

class DeviceMaintain {
	/*  */
	@observable isLoading = false;
	@observable deviceList = [];
	@observable maintainList = [];
	@observable maintainPage = {
		pageIndex: 1,
		pageSize: 10,
	};
	@observable deviceType = '';
	@observable planType = 1;
	@observable maintainType = 1;
	@observable devStatus = 1;
	@observable devId = '';

	/*  */
	@observable isDetail = false;

	/*  */
	@observable currentMain = {};

	/* ----------------  */
	@action async getDevice() {
		/*  */
		this.isLoading = true;
		try {
			let res = await services.gets('getDevice')();
			this.isLoading = false;
			if (isDataExist(res)) {
				this.deviceList = ([{ typeName: '', id: 112211 }]).concat(res.data.data);
				this.deviceType = '';
			}
		} catch (error) {
			console.log(error);
		}
	}
	@action async getMaintain(params = {}) {
		/*  */
		this.isLoading = true;
		try {
			let res = await services.gets('getMaintain')({
				deviceType: this.deviceType,
				planType: this.planType,
				maintainType: this.maintainType,
				...this.maintainPage,
				...params
			});
			this.isLoading = false;
			if (isDataExist(res)) {
				this.maintainList = res.data.data.items;
				this.maintainPage = res.data.data.page;
			}
		} catch (error) {
			console.log(error);
		}
	}

	/*  */
	@action async removeMaintain(params, type) {
		let req = type === 'batch' ? 'batchRemove' : 'singleRemove';
		try {
			let res = await services.posts(req)(params);
			if (isDataExist(res)) {
				message.success('');
			}
		} catch (error) {
			console.log(error);
		}
		this.getMaintain();
	}
	/*  */
	@action async getServeInfo(params) {
		this.currentMain = {};
		try {
			let res = await services.gets('getServeInfo')(params);
			if (isDataExist(res)) {
				this.currentMain = res.data.data;
			}
		} catch (error) {
			console.log(error);
		}
	}

	/*  */
	@action async saveMaintain(params) {
		try {
			let req = this.status === 'edit' ? 'updateMaintain' : 'addMaintain';
			let res = await services.posts(req)(params);
			if (isDataExist(res)) {
				message.success('');
			}
		} catch (error) {
			console.log(error);
		}
		this.getMaintain();
	}

	/* ------------  */
	@observable schemeList = [];
	@observable schemePage = { pageIndex: 1, pageSize: 10 };

	@action async getScheme(params = {}) {
		this.isLoading = true;
		try {
			let data = {
				planType: this.planType,
				maintainType: this.maintainType,
				deviceType: this.deviceType,
				...this.schemePage,
				...params
			};
			let res = await services.gets('getScheme')(data);
			this.isLoading = false;
			if (isDataExist(res)) {
				this.schemeList = res.data.data.items;
				this.schemePage = res.data.data.page;
			}
		} catch (error) {
			console.log(error);
		}
	}
	@action async getSchemaInfo(params) {
		try {
			let res = await services.gets('schemaInfo')(params);
			if (isDataExist(res)) {
				this.currentMain = res.data.data;
			}
		} catch (error) {
			console.log(error);
		}
	}
	@action async saveSchema(params) {
		let req = this.status === 'edit' ? 'updateSchema' : 'saveSchema';
		let data = {
			...params
		};
		try {
			let res = await services.posts(req)(data);
			if (isDataExist(res)) {
				message.success('');
			}
		} catch (error) {
			console.log(error);
		}
		this.getScheme();
	}
	@action async delSchema(params, type) {
		let req = type === 'batch' ? 'batchDelSchema' : 'delSchema';
		try {
			let res = await services.posts(req)(params);
			if (isDataExist(res)) {
				message.success('');
			}
		} catch (error) {
			console.log(error);
		}
		this.getScheme();
	}

	/* -----------  */
	@observable checkPlanList = [];
	@observable checkPlanPage = { pageIndex: 1, pageSize: 10 };
	@observable deviceByIdList = [];
	@observable schemaByIdList = [];

	@action async getPlan(params = {}) {
		this.isLoading = true;
		try {
			let data = {
				typeId: this.deviceType,
				planType: this.planType,
				maintainType: this.maintainType,
				...this.checkPlanPage,
				...params
			};
			let res = await services.gets('getPlan')(data);
			this.isLoading = false;
			if (isDataExist(res)) {
				this.checkPlanList = res.data.data.items;
				this.checkPlanPage = res.data.data.page;
			}
		} catch (error) {
			console.log(error);
		}
	}
	@action async getListByType(type, data) {
		try {
			let req = type === 'device' ? 'getDeviceByType' : 'getSchemaByType';
			let params = { typeId: this.deviceType, ...data };
			if (type === 'schema') params.planType = this.planType;
			let res = await services.gets(req)(params);
			if (isDataExist(res)) {
				if (type === 'device') {
					this.deviceByIdList = res.data.data;
				} else {
					this.schemaByIdList = res.data.data;
				}
			}
		} catch (error) {
			console.log(error);
		}
	}
	@action async savePlan(params) {
		let req = 'savePlan';
		let data = {
			...params
		};
		try {
			let res = await services.posts(req)(data);
			if (isDataExist(res)) {
				message.success('');
			}
		} catch (error) {
			console.log(error);
		}
		this.getPlan();
	}
	@action async delPlan(params, type) {
		let req = type === 'batch' ? 'batchDelPlan' : 'delPlan';
		try {
			let res = await services.posts(req)(params);
			if (isDataExist(res)) {
				message.success('');
			}
		} catch (error) {
			console.log(error);
		}
		this.getPlan();
	}
	@action async getPlanInfo(params) {
		try {
			let res = await services.gets('getPlanInfo')(params);
			if (isDataExist(res)) {
				this.currentMain = res.data.data;
				return res.data.data.excuteTime;
			}
		} catch (error) {
			console.log(error);
		}
	}
	@action async toDetail(params) {
		try {
			let res = await services.posts('toDetail')(params);
			if (isDataExist(res)) {
				message.success('');
			}
		} catch (error) {
			console.log(error);
		}
		this.getPlan();
	}

	/* ----------------- */
	@observable deviceId = '';
	@observable op_deviceList = [];
	@observable operateList = [];
	@observable operatePage = { pageIndex: 1, pageSize: 10 };
	@observable listByContentsIds = [];

	@action async getOperateDevice() {
		try {
			let res = await services.gets('getDeviceList')();
			if (isDataExist(res)) {
				this.op_deviceList = res.data.data
				this.deviceId = '';
			}
		} catch (error) {
			console.log(error);
		}
	}
	@action async getOperateList(params) {
		this.isLoading = true;
		try {
			let data = {
				planType: this.planType,
				maintainType: this.maintainType,
				deviceId: this.deviceId,
				...this.operatePage,
				...params
			};
			// getScheme
			let res = await services.gets('getOperateList')(data);
			this.isLoading = false;
			if (isDataExist(res)) {
				this.operateList = res.data.data.items;
				this.operatePage = res.data.data.page;
			}
		} catch (error) {
			console.log(error);
		}
	}
	@action async getListByContents(params) {
		try {
			let res = await services.gets('getListByContentIds')(params);
			if (isDataExist(res)) {
				this.listByContentsIds = res.data.data;
				return res.data.data;
			}
		} catch (error) {
			console.log(error);
		}
	}
	@action async saveExcution(params) {
		try {
			let res = await services.posts('saveOperate')(params);
			if (isDataExist(res)) {
				message.success('');
			}
		} catch (error) {
			console.log(error);
		}
		this.getOperateList();
	}

	/* -------------------- */
	@observable result = 0
	@observable typeId = 0
	@observable deviceId = 0
	@observable queryList = []
	@observable queryPage = { pageIndex: 1, pageSize: 10, }

	@action async getQuery(params) {
		this.queryList = []
		this.isLoading = true;
		try {
			let data = {
				...this.queryPage,
				planType: this.planType,
				maintainType: this.maintainType,
				result: this.queryResult,
				typeId: this.typeId || '',
				deviceId: this.deviceId || '',
				...params
			}
			let res = await services.gets('getQuery')(data)
			this.isLoading = false;
			if (isDataExist(res)) {
				this.queryList = res.data.data.items;
				this.queryPage = res.data.data.page;
			}
		} catch (error) {
			console.log(error);
		}
	}

	/* ----------------------- */
	@observable monitorList = [];
	@observable monitorPage = {};
	@observable monitorVisible = false;
	@observable deviceSearchId = '';
	@observable detailList = [];
	@observable detailId = '';
	@observable detailInfo = {};

	@action async getMonitor() {
		this.isLoading = true;
		try {
			let res = await services.gets('getMonitor')();
			this.isLoading = false;
			if (isDataExist(res)) {
				this.monitorList = res.data.data;
				// this.monitorPage = res.data.data.page;
			}
		} catch (error) {
		}
	}
	@action async getMonitorDetail() {
		try {
			let res = await services.gets('getMonitorDetail')({ devId: this.detailId })
			if (isDataExist(res)) {
				this.detailList = res.data.data;
			}
		} catch (error) {
			console.log(error);
		}
	}

	/*  */
	@observable devList = []
	@action async getDeviceList() {
		try {
			let res = await services.gets('getDeviceList')()
			if (isDataExist(res)) {
				this.devList = res.data.data;
			}
		} catch (error) {
			console.log(error);
		}
	}

	@action clearData() {
		this.result = 0
		this.typeId = 0
		this.deviceId = 0
		this.queryList = []
		this.queryPage = {}
	}
	@action modifyValue(key, value) {
		this[key] = value
	}

}

let DeviceMaintainStore = new DeviceMaintain();
export default DeviceMaintainStore;
